<template>
	<el-form ref="form" :model="form" style="width: 800px" label-width="100px" size="mini">
		<el-form-item label="发起人名称">
			<el-input v-model="form.username"></el-input>
		</el-form-item>

		<el-form-item label="发起人头像">
			<el-upload
					class="avatar-uploader"
					action="/api/auth/upload"
					:show-file-list="false"
					:with-credentials="true"
					:on-success="handleAvatarSuccess"
			>
				<img v-if="form.portrait" :src="form.portrait" class="avatar">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
			<el-input type="hidden" style="line-height: 0" v-model="form.portrait"></el-input>
		</el-form-item>

		<el-form-item label="项目标题">
			<el-input v-model="form.title" placeholder="顶部标题"></el-input>
		</el-form-item>

		<el-form-item label="项目说明">
			<el-input v-model="form.introduction" placeholder="一大段说明那个......"></el-input>
		</el-form-item>
		<el-form-item label="需要资金">
			<el-input v-model="form.need_amount" placeholder=""></el-input>
		</el-form-item>

		<el-form-item label="患者">
			<el-input v-model="form.patient" placeholder="XXX"></el-input>
		</el-form-item>
		<el-form-item label="所患疾病">
			<el-input v-model="form.sickness" placeholder="XXX"></el-input>
		</el-form-item>

		<el-form-item label="证明医院">
			<el-input v-model="form.prove_hospital" placeholder="XXX医院"></el-input>
		</el-form-item>
		<el-form-item label="收款人">
			<el-input v-model="form.payee" placeholder="XXX(本人)"></el-input>
		</el-form-item>
		<el-form-item label="年收入">
			<el-input v-model="form.annual_income" placeholder="5"></el-input>
		</el-form-item>
		<el-form-item label="金融资产">
			<el-input v-model="form.financial_assets" placeholder="2"></el-input>
		</el-form-item>
		<el-form-item label="房产数量">
			<el-input v-model="form.house_count" placeholder="1"></el-input>
		</el-form-item>
		<el-form-item label="房产价值">
			<el-input v-model="form.house_value" placeholder="20"></el-input>
		</el-form-item>
		<el-form-item label="房产状态">
			<el-input v-model="form.house_status" placeholder="已变卖/未变卖"></el-input>
		</el-form-item>
		<el-form-item label="是否有医保">
			<el-input v-model="form.health_care" placeholder="有医保/无医保"></el-input>
		</el-form-item>

		<el-form-item label="证明图片">
			<el-upload
					action="/api/auth/upload"
					list-type="picture-card"
					:file-list="fileList"
					:on-remove="handleRemove"
					:on-success="handleImageSuccess"
			>
				<i class="el-icon-plus"></i>
			</el-upload>
		</el-form-item>
		<el-form-item>
			<el-button type="primary" @click="onSubmit">立即创建</el-button>
			<el-button>取消</el-button>
		</el-form-item>
	</el-form>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          id: 0,
          username: '',
          portrait: '',
          title: '',
          patient: '',
          sickness: '',
          prove_hospital: '',
          payee: '',
          annual_income: '',
          financial_assets: '',
          house_count: '',
          house_value: '',
          house_status: '',
          health_care: '',
          need_amount: '',
          introduction: '',
          image_list: [],
				},
        fileList: []
      }
    },
    methods: {
      onSubmit() {// eslint-disable-next-line no-mixed-spaces-and-tabs
        this.$httpPost("admin/projectAdd", this.form).then((response) => {
          if (response.code === 0) {
            this.$message({
              message: response.msg,
              center: true,
              type: "success"
            });
            for(let key  in this.form){
              this.form[key] = ''
            }
          } else {
            this.$message({
              message: response.msg,
              center: true,
              type: "warning"
            });
          }
        }).catch(() => {
          this.$message({
            message: '请求服务器异常, 请稍后再试',
            center: true,
            type: "error"
          });
        })
      },
			// eslint-disable-next-line no-unused-vars
      handleAvatarSuccess(res, file){
        if(res.code === 0) {
          this.form.portrait = res.data.file;
        }
      },
			// eslint-disable-next-line no-unused-vars
      handleImageSuccess(res, file){
        if(res.code === 0) {
					this.form.image_list.push(res.data.file)
					console.log(this.project_image_list)
        }
      },
      handleRemove(file, fileList){
         console.log(file, fileList);
      },
    },
	activated() {
		// eslint-disable-next-line no-mixed-spaces-and-tabs
    	let projectId = this.$store.state.projectId

		if( projectId > 0 ) {

		this.$httpGet("project/info?id=" + projectId).then((response) => {
			if (response.code === 0) {
        this.form = response.data.item;
        this.fileList = response.data.fileList;
      }
		})
		}
    }
  }
</script>

